import common from '../../../common/common.js'
import uniPopup from '../../../components/uni-popup.vue'

export default {
	data() {
		return {
			name:'aa',
			userInfo:[],		//用户信息
			scrollLeft:0,
			tabIndex: 0,
			isClickChange:false,
			tabBars: [{
				name: '待配货',
				id: '0'
			}, {
				name: '已配货',
				id: '1'
			}],
			orderList: [],
			popType: 'middle',
			showPopupMiddle: false,
			orderid:0,
			goods_real_weight:0,
			index:'',
			subindex:'',
			order_no:'',
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		

		if(shopInfo == ''){
			uni.redirectTo({
				url: '/pages/userlogin/userlogin',
			});
			return false
		}
		
		that.getMerchantOrderList();
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getUserInfo();
		if(that.userInfo.shop_id > 0){
			that.getMerchantOrderList();
		}
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		//核重弹窗
		pophezhong(e){
			var that = this
			var orderid = e.currentTarget.dataset.orderid
			console.log("orderid",orderid)
			var index = e.currentTarget.dataset.index
			var subindex = e.currentTarget.dataset.subindex
			var order_no = e.currentTarget.dataset.order_no
			that.orderid = orderid
			that.index = index
			that.subindex = subindex
			that.order_no = order_no				
			that.showMiddlePopup();
			
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.showPopupMiddle = true;
			//this.resetAttr();
		},
		hidePopup: function() {
			this.showPopupMiddle = false;
			//this.resetAttr();
		},
		
		//输入重量
		setZhong(e){
			var that = this
			that.goods_real_weight = e.detail.value;
			console.log(that.goods_real_weight)
		},
		
		//确认核重吗
		confirmHezhong(){
			var that = this
			var goods_real_weight = that.goods_real_weight
			if(goods_real_weight <= 0){
				uni.showToast({
					title:'请输入重量',
					duration:1000,
					icon:'none'
				})
				return false
			}
			uni.showModal({
				title:'提示',
				content:'确定要核重吗？',
				success(res) {
				  if (res.confirm) {
					that.hezhong();
				  }
				}
			})
			
		},
		
		//核重
		hezhong(){
			var that = this
			var orderid = that.orderid
			
			var goods_real_weight = that.goods_real_weight
			uni.request({
				url: common.sjApiUrl + 'merchant/updateOrderGoodsWeight',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' 
				},
				data: {
					orderid:orderid,
					goods_real_weight:goods_real_weight
				},
				success: res => {
					var data = res.data
					if(data.code == 0){
						uni.showToast({
							title:'操作成功',
							duration:1000,
							icon:'success'
						})
						that.orderList[that.index].order_realweight = goods_real_weight
						that.goods_real_weight = 0;
						setTimeout(function() {
							that.hidePopup();
						}, 1000)
					}else{
						uni.showToast({
							title:'操作失败'+data.msg,
							duration:1000,
							icon:'none'
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//已配货
		peihuo(e){
			var that = this
			var orderid = e.currentTarget.dataset.orderid
			var index = e.currentTarget.dataset.index
			uni.showModal({
				title:'提示',
				content:'货品已准备好了？',
				success(res) {
					if(res.confirm){
						uni.request({
							url: common.sjApiUrl + 'merchant/peihuo',
							method: 'GET',
							data: {
								orderid : orderid
							},
							success: res => {
								var data = res.data
								if(data.code == 0){
									that.orderList[index].order_peihuo = 1;
									uni.showToast({
										title:'操作成功',
										duration:1000,
										icon:'success'								
									})
									that.getMerchantOrderList();
								}
							},
						});
					}
				}
			})
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.redirectTo({
				url: url
			});
		},
		
		//获取商户订单列表
		getMerchantOrderList(){
			var that = this
			var uid = uni.getStorageSync('userid');
			var shopInfo = uni.getStorageSync('shopInfo');
			var shop_id = shopInfo.shop_id
			console.log("shop_id",shop_id)
			uni.request({
				url: common.sjApiUrl + 'merchant/getMerchantOrderList',
				method: 'GET',
				data: {
					otype:that.tabIndex,
					shop_id:shop_id
				},
				success: res => {
					console.log("res",res)
					that.orderList = res.data.order_list
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		tapTab(index) { //点击tab-bar
			var that = this
			if (that.tabIndex === index) {
				return false;
			} else {					
				that.isClickChange = true;
				that.tabIndex = index;
				that.getMerchantOrderList();
			}
		},
		
		//更新获取用户信息
		getUserInfo(){
			//console.log('获取用户信息');
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			if(shopInfo == null || shopInfo == ''){
				uni.redirectTo({
					url: '/pages/userlogin/userlogin'
				});
				return false
			}
			
			uni.request({
				url: common.sjApiUrl + 'merchant/getUserInfo',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {
					var shop = res.data.userInfo
					console.log(shop)
					if(shop != ''){
						that.shopInfo = shop
						uni.setStorageSync('shopInfo',shop);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
	},
	components: {
		uniPopup
	}
}
